body {
  margin: 0;
  padding: 0;
}
.container {
  margin: 0 auto;
}
.container--header {
  position: relative;
  height: 100%;
}
.visually-hidden,
.visually_hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Bold.ttf");
  font-style: normal;
  font-weight: 700;
}
.header {
  position: relative;
  padding-top: 80px;
  padding-left: 5%;
  padding-right: 5%;
  height: 100vh;
  background-image: url("../img/Guitar copy.jpg");
  background-position: center 10%;
  overflow: hidden;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  height: inherit;
}
.header__list {
  display: flex;
  justify-content: flex-start;
  width: 440px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.header__item {
  position: relative;
  margin-right: 30px;
}
.header__item::after {
  position: absolute;
  content: '';
  top: 6px;
  right: -17px;
  height: 6px;
  width: 6px;
  background-color: #826b60;
  border-radius: 50%;
}
.header__item:last-child::after {
  display: none;
}
.header__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: #241c18;
  font-family: "Lora";
  font-weight: 700;
  font-size: 18px;
}
.header__title-container {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.header__title-wrapper {
  display: flex;
  flex-direction: column;
  z-index: 1;
}
.header__background-wrapper {
  display: flex;
  justify-content: space-between;
}
.header__title {
  display: flex;
  align-self: center;
  margin: 0;
  font-family: "Oswald";
  font-weight: 700;
  font-size: 7vw;
  color: #e4dddb;
  text-transform: uppercase;
}
.header__title--white {
  color: #241c18;
}
.header__background {
  position: absolute;
}
.header__background-text {
  position: relative;
  flex-basis: 10%;
  padding: 0;
  text-align: center;
  font-size: 1vw;
  font-family: "Lora";
  font-weight: 400;
  text-transform: uppercase;
  line-height: 26px;
  opacity: 0.1;
  z-index: 0;
}
.header__background-text:first-child,
.header__background-text:last-child {
  bottom: 100px;
}
.header__background-text:nth-child(2) {
  bottom: 150px;
}
.header__background-text:nth-child(3) {
  bottom: 125px;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald-bold.woff2") format("woff2"), url("../fonts/oswald-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Lora";
  src: url("../fonts/lora-bold.woff2") format("woff2"), url("../fonts/lora-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Lora";
  src: url("../fonts/lora-regular.woff2") format("woff2"), url("../fonts/lora-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
